.footer{
    position: relative;
    width: 100%;
    background: url("../img/footer-banner.webp");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    .content{
        padding: 190px 100px 100px 100px;
        @media screen and (min-width: 640px) and (max-width: 920px) {
            padding: 70px 100px 25px 100px;
        }
        @media screen and (min-width: 300px) and (max-width: 640px) {
            padding: 70px 25px 25px 25px;
        }
        .top{
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            p{
                color: $white;
                font-size: 30px;
                text-align: center;
                letter-spacing: 0.09em;
                line-height: 50px;
                @media screen and (min-width: 920px) and (max-width: 1440px) {
                    font-size: 20px;
                    line-height: 30px;
                }
                @media screen and (min-width: 640px) and (max-width: 920px) {
                    font-size: 15px;
                    line-height: 20px;
                }
                @media screen and (min-width: 300px) and (max-width: 640px) {
                    font-size: 10px;
                    line-height: 20px;
                }
            }
            ul{
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                align-items: center;
                li{
                    margin-right: 155px;
                    list-style-type: none;
                    width: 80px;
                    height: 80px;
                    @media screen and (min-width: 920px) and (max-width: 1440px) {
                        width: 30px;
                        height: 30px;
                        margin-right: 100px;
                        img {
                            width: 30px;
                            height: 30px;
                        }
                    }
                    @media screen and (min-width: 640px) and (max-width: 920px) {
                        width: 15px;
                        height: 15px;
                        margin-right: 50px;
                        img {
                            width: 15px;
                            height: 15px;
                        }
                    }
                    @media screen and (min-width: 300px) and (max-width: 640px) {
                        width: 15px;
                        height: 15px;
                        margin-right: 20px;
                        img {
                            width: 15px;
                            height: 15px;
                        }
                    }
                    &:nth-child(1){
                        width: 95px;
                        @media screen and (min-width: 920px) and (max-width: 1440px) {
                            width: 45px;
                            img {
                                width: 45px;
                            }
                        }
                        @media screen and (min-width: 640px) and (max-width: 920px) {
                            width: 30px;
                            img {
                                width: 30px; 
                            }
                        }
                        @media screen and (min-width: 300px) and (max-width: 640px) {
                            width: 30px;
                            img {
                                width: 30px; 
                            }
                        }
                    }
                    &:nth-child(4){
                        width: 75px;
                        @media screen and (min-width: 920px) and (max-width: 1440px) {
                            width: 25px;
                            img {
                                width: 25px;
                            }
                        }
                        @media screen and (min-width: 640px) and (max-width: 920px) {
                            width: 15px;
                            img {
                                width: 15px;
                            }
                        }
                        @media screen and (min-width: 300px) and (max-width: 640px) {
                            width: 15px;
                            img {
                                width: 15px;
                            }
                        }
                    }
                    &:nth-child(5){
                        margin-right: 0;
                    }
                }
            }
        }
        .btns{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: row;
            margin-top: 45px;
            @media screen and (min-width: 300px) and (max-width: 640px) {
                margin-top: 10px;
            }
            a{
                padding: 23px 84px;
                font-size: 22px;
                letter-spacing: 0.09em;
                color: $yellow;
                background: rgba(231, 185, 22, 0.25);
                border: 5px solid $yellow;
                box-sizing: border-box;
                box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.7);
                backdrop-filter: blur(10px);
                border-radius: 50px;
                text-decoration: none;
                margin-right: 50px;
                cursor: pointer;
                text-align: center;
                &:nth-child(3){
                    margin-right: 0;
                }
                @media screen and (min-width: 920px) and (max-width: 1440px) {
                    font-size: 17px;
                }
                @media screen and (min-width: 640px) and (max-width: 920px) {
                    padding: 15px 20px;
                    font-size: 10px;
                    border: 2px solid $yellow;
                }
                @media screen and (min-width: 300px) and (max-width: 640px) {
                    padding: 10px 30px;
                    font-size: 5px;
                    border: 2px solid $yellow;
                    margin-right: 30px;
                }
            }
        }
    }
}

.modal {
    display: none;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    justify-content: center;
    align-items: center;
}
