.m-btns{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 40px;
    @media screen and (min-width: 300px) and (max-width: 640px) {
        margin-top: 10px;
    }
    a{
        width: 50%;
        text-align: center;
        box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
        backdrop-filter: blur(10px);
        border-radius: 40px;
        padding: 18px 0;
        font-size: 30px;
        text-decoration: none;
        margin-right: 30px;
        cursor: pointer;
        @media screen and (min-width: 300px) and (max-width: 640px) {
            font-size: 10px;
            margin-right: 10px;
            padding: 5px 0;
        }
        &:nth-child(1){
            background: rgba(67, 127, 151, 0.25);
            border: 5px solid $green;
            color: $green;
            @media screen and (min-width: 300px) and (max-width: 640px) {
                border: 2px solid $green;
            }
        }
        &:nth-child(2){
            background: rgba(186, 45, 11, 0.25);
            border: 5px solid $red;
            color: $red;
            margin-right: 0;
            @media screen and (min-width: 300px) and (max-width: 640px) {
                border: 2px solid $red;
            }
        }
    }
}

.m-btns button{
    width: 50%;
    text-align: center;
    -webkit-box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
    box-shadow: 30px 30px 50px rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: 40px;
    padding: 18px 0;
    font-size: 30px;
    text-decoration: none;
    margin-right: 30px;
    cursor: pointer;
}

.m-btnsSend{
    background: rgba(67, 127, 151, 0.25);
    border: 5px solid #00272b;
    color: #00272b;
}

